.modal {
    height: 100vh;
    width: 100vw;
    /* background-color: rgba(0, 0, 0, 0.5); */
    background-color: rgba(6,15,49,0.9);
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
    opacity: 0;
    pointer-events: none;
    transition: 0.5s;
    overflow: auto;
    padding: 1rem;
}

.modal.active {
    opacity: 1;
    pointer-events: all;
}

.modalContent {
    padding: 20px;
    border-radius: 12px;
    background-color: white;
    transform: scale(0.5);
    transition: 0.4s all;
    /* min-width: 50vw; */

    max-width: rem(1100);
    margin: auto;

    position: relative;
}

.modalClose {
    padding: 0;
    background: none;
    border: none;
    width: 0;
    height: 0;
    min-width: 0;
    cursor: pointer;

    position: absolute;
    top: 1rem;
    right: 3rem;
    z-index: 2;

    &:hover {
    opacity: 0.7;
    }
}

.modalContent.active {
    transform: scale(1);
}